(function () {
  'use strict';

  angular
    .module('app')
    .directive('animateOnLoad', animateOnLoad);

//  animateOnLoad.$inject = ['dependencies'];

  /* @ngInject */
  function  animateOnLoad($animateCss) {
    var directive = {
      link: link,
      restrict: 'A'
    };
    return directive;

    function link(scope, element) {
      $animateCss(element, {
        'event': 'enter',
        structural: true
      }).start();
    }
  }
})();
(function() {
  'use strict';

  angular
    .module('app')
    .controller('WebsitesViewController', WebsitesViewController);

//  WebsitesViewController.$inject = ['dependencies'];

  /* @ngInject */
  function WebsitesViewController($scope, $rootScope, dataService, websiteService, $timeout, $stateParams){
    var vm = this;
    var doneStagger;

    $scope.$on('$destroy', function (){
      $timeout.cancel(doneStagger);
    });

    activate();

    ////////////////

    function activate() {
      staggerPage();
      if(!websiteService.websites)
        websiteService.getWebsites().then(function(websites){
          vm.website = getWebsite($stateParams.websiteName, websites);
        });
    }

    function staggerPage() {
      $rootScope.activeTab = 'Websites';
      //Animate the intro text
      doneStagger = $timeout(function(){
        vm.showWebsite = true;
        $rootScope.$broadcast('DONE_STAGGER');
      }, 250);
    }

    function getWebsite(websiteName, websites) {
      for(var i in websites) {
        var website = websites[i];
        if(websiteService.getUrl(website.title) === websiteName) {
          return website;
        }
      }
    }
  }
})();

(function() {
    'use strict';

    angular
        .module('app')
        .factory('websiteService', websiteService);

    // websiteService.$inject = ['dependencies'];

    /* @ngInject */
    function websiteService(dataService) {
      var websites = null;

      var service = {
          websites: websites,
          getUrl: getUrl,
          getWebsites: getWebsites
      };

      return service;

      function getUrl(title) {
        title = title.toLowerCase();
        title = title.replace(/ /g, '-');
        return title;
      }

      function getWebsites() {
        return dataService.get('/data/websites.json').then(function(data){
          websites = data;
          return websites;
        });
      }
    }
})();

