(function() {
  'use strict';

  angular
    .module('app')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1465962914930;
    vm.showToastr = showToastr;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

(function () {
  'use strict';
  angular
    .module('app')
    .factory('dataService', dataService);

//  dataService.$inject = ['dependencies'];

  /* @ngInject */
  function  dataService($http){
    var exports = {
      get: get,
      post: post
    };

    return exports;

    ////////////////

    function get(url) {
      return $http({
        method: 'GET',
        url: url,
        cache: true
      }).then(function(response){
        return response.data;
      });
    }

    function post(url, params) {
      return $http({
        method: 'POST',
        url: url,
        params: params
      }).then(function(response){
        return response.data;
      });
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('app')
    .controller('HeaderController', HeaderController);

//  HeaderController.$inject = ['dependencies'];

  /* @ngInject */
  function HeaderController($state, $rootScope, $timeout){
    var vm = this;

    var stateChangeStart = $rootScope.$on('$stateChangeStart',  function(event, toState, toParams, fromState, fromParams, options){
      if(!vm.loaded && fromParams.websiteName || !vm.loaded && fromParams.projectName)
        event.preventDefault();
    });

    vm.menu = [
      {
        title: 'About',
        sref: 'root.about'
      },{
        title: 'Projects',
        sref: 'root.projects'
      },{
        title: 'Websites',
        sref: 'root.websites'
      },{
        title: 'Experience',
        sref: 'root.experience'
      },{
        title: 'Contact',
        sref: 'root.contact'
      }
    ];

    vm.go = function(sref) {
       $state.go(sref);
    };

    activate();

    ////////////////

    function activate() {
      $timeout(function(){
        vm.loaded = true;
      }, 150)
    }


  }
})();

(function() {
  'use strict';

  angular
    .module('app')
    .controller('FooterController', FooterController);

//  FooterController.$inject = ['dependencies'];

  /* @ngInject */
  function FooterController($rootScope, $timeout){
    var vm = this;
    var footerTimer;

    //When the state changes, hide the footer
    var stateChangeStart = $rootScope.$on('$stateChangeStart',  function(){
      $timeout.cancel(footerTimer);
      vm.showFooter = false;
    });

    //When we are done staggering, show the footer
    var doneStagger = $rootScope.$on('DONE_STAGGER',  function(){
      showFooter();
    });

    activate();

    ////////////////

    function activate() {
      // showFooter();
    }

    //Show the footer 1 second after everything else has finished animating
    function showFooter() {
      footerTimer = $timeout(function(){
        vm.showFooter = true;
      }, 1000);
    }
  }
})();


